
import { defineComponent } from 'vue'
import magnifier from './directives/magnifier'
// import { magnifierProps } from './Magnifier'

export default defineComponent({
  name: 'df-magnifier',
  props: {
    imgWidth: {
      type: Number || String,
      default: 375,
    },
    imgHeight: {
      type: Number || String,
      default: 600,
    },
    magWidth: {
      type: Number || String,
      default: 100,
    },
    magHeight: {
      type: Number || String,
      default: 100,
    },
    magRadius: {
      type: String,
      default: '50%',
    },
    blank: {
      type: Boolean,
      default: false,
    },
    link: {
      type: String,
    },
    imgAlt: {
      type: String,
    },
    imgUrl: {
      type: String,
    },
    cursor: {
      type: String,
    },
    scale: {
      type: Number,
    },
  },
  directives: {
    magnifier,
  },
})
